import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import getShortages from '../controllers/shortages_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /shortages:
 *   get:
 *     tags:
 *       - Shortages
 *     description: Returns list of Shortages
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "query"
 *         name: "search"
 *         description: "Search text"
 *         schema:
 *           type: object
 *           properties:
 *              search:
 *                  type: string
 *       - in: "query"
 *         name: "limit"
 *         description: "Sets the number of objects to be returned"
 *         schema:
 *           type: object
 *           properties:
 *              limit:
 *                  type: number
 *       - in: "query"
 *         name: "offset"
 *         description: "Sets the offset to the list of breakages"
 *         schema:
 *           type: object
 *           properties:
 *              offset:
 *                  type: number
 *       - in: "query"
 *         name: "from"
 *         description: "from date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *       - in: "query"
 *         name: "to"
 *         description: "to date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *          type: array
 *          items:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            serialNumber:
 *              type: string
 *            caseBarCode:
 *              type: string
 *            productionCode:
 *              type: string
 *            productionName:
 *              type: string
 *            batchNumber:
 *              type: string
 *            date:
 *              type: date
 */

/**
 * @swagger
 * /shortages:
 *   post:
 *     tags:
 *       - Shortages
 *     description: Returns success message if Shortages details are added
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *            serialNumber:
 *              type: string
 *            caseBarCode:
 *              type: string
 *            productionCode:
 *              type: string
 *            productionName:
 *              type: string
 *            batchNumber:
 *              type: string
 *            date:
 *              type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            serialNumber:
 *              type: string
 *            caseBarCode:
 *              type: string
 *            productionCode:
 *              type: string
 *            productionName:
 *              type: string
 *            batchNumber:
 *              type: string
 *            date:
 *              type: date
 */

/**
 * @swagger
 * /shortages:
 *   put:
 *     tags:
 *       - Shortages
 *     description: Returns success message if Shortages details are updated
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *          type: array
 *          items:
 *            type: object
 *            properties:
 *              _id:
 *                type: string
 *              update:
 *                type: object
 *                properties:
 *                  serialNumber:
 *                    type: string
 *                  caseBarCode:
 *                    type: string
 *                  productionCode:
 *                    type: string
 *                  productionName:
 *                    type: string
 *                  batchNumber:
 *                    type: string
 *                  date:
 *                    type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            message:
 *              type: string
 */

router.get(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  getShortages
);

export default router;
